import React from 'react';
import {
    MemberBadges,
    MemberCard,
    MemberCardContainer,
    MemberDetails,
    MemberImage,
    MemberInfo,
    MemberSocials
} from "./MemberTeamCard.styles";
import {MemberTeamCardTypes} from "./MemberTeamCardTypes";
import {Badge, BadgeColors, BadgeSizes, BadgeTypes, H3, P} from "@firstbreath/design-system";

export const MemberTeamCard = ({
                                   name, socials, image, description, badges,
                               }: MemberTeamCardTypes) => {

    return <MemberCardContainer>
        <MemberCard>
            <MemberImage src={image}/>
            <MemberInfo>
                <MemberDetails>
                    <H3>{name}</H3>
                    <P>{description}</P>
                    <MemberBadges>
                        {badges && badges.map((badge) => {
                            return <Badge
                                type={BadgeTypes.PILL}
                                color={BadgeColors.PRIMARY}
                                size={BadgeSizes.SMALL}
                            >
                                {badge.text}
                            </Badge>
                        })}
                    </MemberBadges>
                </MemberDetails>
                <MemberSocials>
                    {socials.map((social) => <a href={social.url} target="_blank" rel="noreferrer">{social.logo}</a>)}
                </MemberSocials>
            </MemberInfo>
        </MemberCard>
    </MemberCardContainer>
}