import React from 'react';
import {NavBarProps} from "./NavBar.types";
import {
    NavBarContainer,
    NavBarIcon,
    NavBarLanguageDropdown,
    NavBarLink,
    NavBarLinks,
    NavBarStyle
} from "./NavBar.styles";
import {useLocation} from "react-router-dom";
import {useTranslation} from "react-i18next";
import {Option} from "@firstbreath/design-system/dist/components/Dropdown/Dropdown.types";

export const NavBar = ({links}: NavBarProps) => {
    const location = useLocation();
    const {i18n} = useTranslation()
    const {pathname} = location;

    const [scrollPosition, setScrollPosition] = React.useState(0);
    const handleScroll = () => {
        const position = window.pageYOffset;
        setScrollPosition(position);
    };

    React.useEffect(() => {
        window.addEventListener('scroll', handleScroll, {passive: true});

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    const languageOptions = [
        {
            display: <img src="/images/icons/flags/frensh.png" alt="french flag" width="25"/>,
            value: 'fr'
        },
        {
            display: <img src="/images/icons/flags/english.png" alt="english flag" width="25"/>,
            value: 'en'
        }
    ]

    const onLanguageSelect = (_: Option, index?: number | undefined) => {
        if (index === undefined)
            return;
        const selectedLanguage = languageOptions[index]
        console.log(selectedLanguage.value);
        i18n.changeLanguage(selectedLanguage.value)
    }

    return (
        <NavBarStyle $onTop={scrollPosition !== 0}>
            <NavBarContainer>
                <NavBarIcon src="/images/logo.png" alt={"First Breath Logo"}/>
                <NavBarLinks>
                    {links.map((link, index) => (
                        <NavBarLink key={index} to={link.url} $isActive={link.url === pathname}>{link.name}</NavBarLink>
                    ))}
                    <NavBarLanguageDropdown
                        $onTop={scrollPosition !== 0}
                        options={languageOptions.map(e => e.display)}
                        onSelect={onLanguageSelect}
                        defaultSelected={languageOptions[0].display}
                    />
                </NavBarLinks>
            </NavBarContainer>
        </NavBarStyle>
    )
}
